        .catch(err => {
          console.log(err)
          console.log(' ')
        })
    },
    renderC3 () {
      const vm = this
      // 
      const total = {} //  ""  ""
      vm.orderData.forEach(function (orderItem) {
        orderItem.products.forEach(function (productItem) {
          if (total[productItem.title] === undefined) {
            total[productItem.title] = productItem.price * productItem.quantity
          } else {
            // """total."
            total[productItem.title] += productItem.price * productItem.quantity
          }
        })
      })
      // 
      const titleAry = Object.keys(total) // ""

      //  total + titleAry  C3 
      const productAry = [] //  ""  ""
      titleAry.forEach(function (title) {
        const ary = []
        ary.push(title)
        ary.push(total[title])
        productAry.push(ary)
      })

      // c3: columns
      const newAry = vm.arrangeAry(productAry) //  "&"  ""  ""

      // C3: colors
      const newColors = {} // C3

      newAry.forEach((item, index) => {
        if (item[0] === '') {
          newColors[''] = '#d1dddb'
        } else {
          if (index === 0) {
            newColors[`${item[0]}`] = '#5f6d89'
          } else if (index === 1) {
            newColors[`${item[0]}`] = '#374157'
          } else if (index === 2) {
            newColors[`${item[0]}`] = '#7f8fb0'
          }
        }
      })

      // C3.js
      const chart = c3.generate({
        bindto: '#chart', // HTML 
        data: {
          type: 'pie',
          columns: newAry,
          colors: newColors
        }
      })
      console.log(' object', chart)
    },
    arrangeAry (ary) {
      const newAry = [] // new Array()

      // ary()
      ary.sort(function (a, b) {
        return b[1] - a[1] //  total
      })

      let otherTotal = 0 //  "" 

      // ""
      ary.forEach((item, index) => {
        if (index < 3) {
          newAry.push(ary[index])
        } else {
          otherTotal += ary[index][1]
        }
      })
      newAry.push(['', otherTotal])
      return newAry
    },
    deleteAll () {
      const vm = this
      axios.delete(`${vm.api_URL}/admin/${vm.api_path}/orders`,
        {
          headers: {
            Authorization: vm.token
          }
        })
        .then(res => {
          console.log(' ')
          vm.getOrderList()
          emitter.emit('getAlertMessage', '')
        })
        .catch(err => {
          console.log(err)
          console.log(' ')
        })
